<template>
  <div class="ds-login-page">
    <div class="ds-login-page-header">
      <img class="logo" :src="logo | imgFormat" />
      <!---->
      <svg viewBox="0 50 500 35" preserveAspectRatio="none" xmlns="http://www.w3.org/2000/svg">
        <path fill="#fff" d="M 0 70 Q 87.5 47, 175 70 T 350 75 T 500 70 V 100 H 0 V 0"></path>
      </svg>
      <!---->
      <div class="ds-login-page-header-bg">
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
    </div>
    <div class="ds-login-page-body">
      <div class="ds-login-page-tab-header">
        <i ref="tabBar" v-if="tabPositions.length" :style="tabPositions[tabActive]"></i>
        <template v-for="(item, index) in tabs">
          <div ref="tabs" :key="index" class="ds-login-page-tab-header-item" :class="{active: tabActive == index}" @click="tabClick(index)">{{ item }}</div>
        </template>
      </div>
      <div class="ds-login-page-tab-body">
        <slot>
          <div v-if="tabActive == 0" class="ds-login-page-tab-body-item">
            <form class="ds-login-page-form" @submit.prevent>
              <div class="ds-login-page-form-item">
                <input v-model.trim="username" type="text" name="username" autocomplete="off" :placeholder="userNamePlaceholder" />
              </div>
              <div class="ds-login-page-form-item">
                <i class="iconfont icon-xiayibu" @click.stop="submitByUsername"></i>
                <input v-model.trim="password" type="password" name="password" autocomplete="off" :placeholder="passwordPlaceholder" />
              </div>
              <input type="submit" style="display: none" @click="submitByUsername" />
            </form>
          </div>
          <div v-if="tabActive == 1" class="ds-login-page-tab-body-item">
            <form class="ds-login-page-form" @submit.prevent>
              <div class="ds-login-page-form-item">
                <div class="getCodeBt" :class="[codeBtType]" @click.stop="getCode">
                  {{ codeBtType != '' ? `${countdownTime}s` : '' }}
                </div>
                <input v-model.trim="phoneNumber" type="tel" name="phoneNumber" maxlength="20" autocomplete="off" :placeholder="phonePlaceholder" />
              </div>
              <div class="ds-login-page-form-item">
                <i class="iconfont icon-xiayibu" @click.stop="submitByPhone"></i>
                <input v-model.trim="phoneCode" type="email" name="phoneCode" maxlength="12" autocomplete="off" :placeholder="SMSCodePlaceholder" />
              </div>
              <input type="submit" style="display: none" @click="submitByPhone" />
            </form>
          </div>
        </slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DsfMobilePlatformLogin",
  data() {
    return {
      logo:
        dsf.config.setting_teas_mob_logo_picture ||
        dsf.url.getWebPath("~/static/img/mobile-logo-login.png"),
      tabs: ["", ""],
      tabPositions: [],
      tabActive: 0,
      // countdown
      codeBtType: "",
      // 
      countdownTimes: 120,
      // 
      countdownTime: 0,
      username: "",
      password: "",
      phoneNumber: "",
      phoneCode: ""
    };
  },
  props: {
    login: {
      type: Function,
      default: null
    },
    userNamePlaceholder: {
      type: String,
      default: ""
    },
    passwordPlaceholder: {
      type: String,
      default: ""
    },
    phonePlaceholder: {
      type: String,
      default: ""
    },
    SMSCodePlaceholder: {
      type: String,
      default: ""
    }
  },
  mounted() {
    this.resize();
    window.addEventListener("resize", this.resize);
  },
  destroyed() {
    clearInterval(this.timeout);
    window.removeEventListener("resize", this.resize);
  },
  methods: {
    resize() {
      let tabBarWidth = 100;
      this.tabPositions = this.$refs.tabs.map((el) => {
        let scaleX = (el.offsetWidth - 6) / tabBarWidth;
        let left = el.offsetLeft + el.offsetWidth / 2 + "px";
        return {
          display: "block",
          transform: `translateX(${left}) scale3d(${scaleX}, 1, 1)`
        };
      });
    },
    tabClick(index) {
      this.$refs.tabBar.style.transition = "transform .2s ease-in-out";
      setTimeout(() => {
        this.tabActive = index;
        setTimeout(() => {
          this.$refs.tabBar.style.transition = "none";
        }, 210);
      }, 0);
    },
    // 
    getCode() {
      if (this.codeBtType != "") return;
      if (this.phoneNumber === "") {
        dsf.layer.message("", false);
        return;
      }
      if (!dsf.validate("isMobileOrTelPhone", this.phoneNumber)) {
        dsf.layer.message("", false);
        return;
      }
      let loading = dsf.layer.loading();
      // todo setTimeout
      setTimeout(() => {
        // 
        this.codeBtType = "countdown";
        dsf.layer.closeLoading(loading);
        this.doCountdown();
      }, 1000);
    },
    // 
    doCountdown() {
      this.countdownTime = this.countdownTimes - 1;
      this.timeout = setInterval(() => {
        this.countdownTime--;
        if (this.countdownTime <= -1) {
          this.codeBtType = "";
          clearInterval(this.timeout);
        }
      }, 1000);
    },
    // 
    submitByUsername() {
      if (!this.login) {
        if (this.username === "") {
          dsf.layer.message("", false);
          return;
        }
        if (this.password === "") {
          dsf.layer.message("", false);
          return;
        }
        this.loginByUsername();
      } else if (dsf.type(this.login) == "function") {
        this.login({
          username: this.username,
          password: this.password,
          type: "usernameAndPassword"
        }).then((res) => {
          this.setStateIsLogin(res.data);
        });
      }
    },
    loginByUsername() {
      let loading = dsf.layer.loading();
      this.$webAPI
        .login({
          userName: this.username,
          password: dsf.md5(dsf.md5(this.password)),
          code: "good"
        })
        .then(({ success, message }) => {
          if (success) {
            this.setStateIsLogin();
          } else {
            dsf.layer.message(message, false);
          }
        })
        .catch((err) => {
          if (err) {
            dsf.layer.message(err.message || "", false);
          }
        })
        .finally(() => {
          dsf.layer.closeLoading(loading);
        });
    },
    // 
    submitByPhone() {
      if (!this.login) {
        if (this.phoneNumber === "") {
          dsf.layer.message("", false);
          return;
        }
        if (!dsf.validate("isMobileOrTelPhone", this.phoneNumber)) {
          dsf.layer.message("", false);
          return;
        }
        if (this.phoneCode === "") {
          dsf.layer.message("", false);
          return;
        }
        this.loginByPhone();
      } else if (dsf.type(this.login) == "function") {
        this.login({
          username: this.username,
          password: this.password,
          type: "phoneAndSMSCode"
        }).then((res) => {
          this.setStateIsLogin(res.data);
        });
      }
    },
    loginByPhone() {
      // todo 
      dsf.layer.message("", false);
    },
    // cookie
    async setStateIsLogin(data) {
      //groupGo
      let groupGo = data.groupGo;
      let appId = this.$root.getAppId();
      let toUrl = "";
      let from = this.$route.query.from;
      if (groupGo) {
        let urlInfo = dsf.url.parse(groupGo);
        if (urlInfo.searchQuery["appid"]) {
          appId = urlInfo.searchQuery["appid"];
          delete urlInfo.searchQuery["appid"];
        }
        toUrl = (urlInfo.protocol ? urlInfo.origin : urlInfo.hostname) + urlInfo.pathname + (dsf.isEmptyObject(urlInfo.searchQuery) ? "" : "?" + dsf.url.queryStringStringify(urlInfo.searchQuery)) + urlInfo.hash;
      }
      //app
      await this.$root.initApp(appId);
      if (from) {
        toUrl = decodeURIComponent(from);
      } else {
        if (!toUrl) {
          //
          toUrl = window.location.pathname;
          if (window.location.search) {
            toUrl += window.location.search;
          }
        }
        let toUrlInfo = dsf.url.parse(toUrl);
        //
        if (!toUrlInfo.hash) {
          toUrl += "#" + this.$root.navs[0].href;
        }
        location.replace(toUrl);
      }
    }
  }
};
</script>