<template>
  <div class="ds-control mobile ds-hometop" :style="style">
    <div class="ds-hometop-logo">
      <img v-if="logoSrc" :src="$logoSrc" />
    </div>
    <div class="ds-hometop-tools">
      <template v-if="buttons.length<=2">
        <DsfIcon v-for="(button,index) in buttons" @click="clickHandler(button,$event)" :key="index" :name="getIcon(button.icon)"></DsfIcon>
      </template>
      <template v-else>
        <DsfIcon v-if="buttons[0]" :key="0" :name="getIcon(buttons[0].icon)" @click="clickHandler(buttons[0],$event)"></DsfIcon>
        <van-popover v-model="showPopover" class="menu" @open="open($event)" ref="popover" trigger="click" :offset="[10,5]" placement="bottom-end">
          <template #reference>
            <DsfIcon name="gengduo" class="ds-hometop-more"></DsfIcon>
          </template>
          <template>
            <template v-for="(button,index) in buttons">
              <div v-if="index>0" class="ds-hometop-menu-item van-popover__action" :key="index" @click="clickHandler(button,$event)">
                <DsfIcon :name="getIcon(button.icon)"></DsfIcon>
                {{button.text}}
              </div>
            </template>
          </template>
        </van-popover>
      </template>
    </div>
    <!-- <div v-show="false">
      <DsfMobileButtonBar :buttons="buttons" refs="buttons" :max-number="999"></DsfMobileButtonBar>
    </div> -->

  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileHomeTop",
  ctrlCaption: "",
  mixins: [$mixins.control, $mixins.buttonBar],
  data() {
    return {
      logoSrc: dsf.config.setting_public_mobile_logo_image || dsf.config.setting_public_logo_image || "",
      showPopover: false,
      offset: [],
      buttonHandler: dsf.buttons.getButtonHander,
      client: "mobile"
    };
  },
  props: {
    buttons: {
      type: Array,
      default: () => []
    },
    padding: {
      type: Array,
      default: () => ["8.5px", "15px", "8.5px", "15px"]
    }
  },
  computed: {
    style() {
      return {
        "padding-left": this.pxToRem(this.padding[3]),
        "padding-right": this.pxToRem(this.padding[1]),
        "padding-top": this.pxToRem(this.padding[0]),
        "padding-bottom": this.pxToRem(this.padding[2])
      };
    },
    $logoSrc() {
      return this.$replace(this.logoSrc);
    }
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {},
  methods: {
    async clickHandler(button, $event, response) {
      var fn = buttonBar.methods.clickHandler;
      await fn.call(this, button, $event, response);
      this.showPopover = false;
    },
    getIcon(icon) {
      return icon.replace(/^icon-/, "");
    },
    open(v) {
      this.$nextTick(() => {
        let el = this.$refs.popover.$refs.popover.$el;
        let arrow = el.querySelector(".van-popover__arrow");
        arrow.style.right = "8px";
      });
    }
  },
  beforeDestroy() {},
  destroyed() {}
});
</script>