<template>
  <div class="ds-control mobile ds-newsinfo">
    <slot name="title">
      <div v-if="!!data.title" class="ds-newsinfo-title">{{ data.title }}</div>
    </slot>
    <slot name="desc"></slot>
    <div class="ds-newsinfo-content" v-html="content"></div>
    <van-empty description="" v-if="!data.title && !data.content" />
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileNewsInfo",
  mixins: [$mixins.control],
  ctrlCaption: "",
  props: {
    url: {
      type: String,
      default: ""
    },
  },
  data() {
    return {
        source: {},
    };
  },
  computed: {
      data() {
          if(typeof this.source === 'string') {
              return {
                content: this.source,
              }
          } else {
              return {
                content: this.source?.content,
                title: this.source?.title,
              }
          }
      },
      content() {
        if(this.data.content && typeof this.data.content === 'string') {
            return this.data.content.replace(/(\d+)px/g, (s, t) => {
                s = s.replace('px', '');
                const value = parseInt(s) * 0.01;
                return `${value}`;
            });
        }
        return '';
      }
  },
  created() {
    if (this.url) {
      this.loadData();
    }
  },
  methods: {
    params() {
        return { id: this.$route.query.id };
    },
    loadData() {
      this.$http.get(this.url, this.params).then(({ data }) => {
        this.source = data.data;
      });
    },
  },
});
</script>