<template>
  <div class="ds-control mobile ds-advancedsearch">
    <!--<form action="javascript:return true" @submit.prevent></form>-->
    <div class="ds-advancedsearch-header">
      <van-search
        v-model="keyword"
        :label="label"
        :shape="shape"
        :background="background2"
        :maxlength="maxlength < 0 ? Infinity : maxlength"
        :placeholder="placeholder"
        :clearable="clearable"
        :clear-trigger="clearTrigger"
        :autofocus="autofocus"
        :show-action="showAction"
        :action-text="actionText"
        :disabled="disabled"
        :readonly="readonly"
        :error="error"
        :input-align="inputAlign"
        :left-icon="leftIcon"
        :right-icon="rightIcon"
        @focus="isSearching = true"
        @search="onSearch"
        @input="$onChange"
        @cancel="onCancel"
      >
        <template v-slot:left>
          <slot name="left"></slot>
        </template>
        <template v-slot:action>
          <slot name="action">
            <span @click="onCancel"></span>
          </slot>
        </template>
        <template v-slot:label>
          <slot name="label"></slot>
        </template>
        <template v-slot:left-icon>
          <slot name="left-icon"></slot>
        </template>
        <template v-slot:right-icon>
          <slot name="right-icon"></slot>
        </template>
      </van-search>
    </div>
    <div v-if="isSearching" class="ds-advancedsearch-body">
      <!---->
      <ul v-if="keyword.length" class="cascade-words">
        <li v-for="(item, index) in cascadeKeywords" :key="index" class="cascade-words-item van-hairline--bottom">
          <i class="iconfont icon-sousuo"></i>
          <div class="cascade-words-item-content">
            <div class="ds-multi-ellipsis-1" v-html="item.html"></div>
          </div>
        </li>
      </ul>
      <template v-else>
        <!---->
        <div class="hot-words ds-advancedsearch-section">
          <div class="ds-advancedsearch-section-title"></div>
          <div class="hot-words-box">
            <div class="hot-words-list">
              <div v-for="(hot, index) in hotKeywords" :key="index" class="hot-words-item" @click="hotClick(hot)">
                <span class="index">{{ index }}</span>
                <div class="content">
                  <div class="ds-multi-ellipsis-1">{{ hot.text }}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!---->
        <div class="history-words ds-advancedsearch-section">
          <div class="ds-advancedsearch-section-title"></div>
          <div class="history-words-box">
            <div class="history-words-list">
              <div v-for="(history, index) in historyKeywords" :key="index" class="history-words-item" @click="historyClick(hot)">
                <div>{{ history.text }}</div>
              </div>
            </div>
          </div>
          <div class="history-words-bts">
            <div class="history-words-bt" @click="clearHistory">
              <i class="iconfont icon-shanchu"></i>
              <span></span>
            </div>
          </div>
        </div>
      </template>
    </div>
    <div v-if="!isSearching" class="ds-advancedsearch-body">
      <!---->
      <van-empty description="" v-if="!searchResult.length" />
      <!--todo -->
    </div>
  </div>
</template>

<script>
export default dsf.component({
  name: 'DsfMobileAdvancedSearch',
  ctrlCaption: '',
  mixins: [$mixins.control],
  props: {
    // 
    label: {
      type: String,
      default: ''
    },
    // (square)(round)
    shape: {
      type: String,
      default: 'square'
    },
    // 
    background: {
      type: String,
      default: 'theme'
    },
    //  -1 
    maxlength: {
      type: [Number, String],
      default: -1,
    },
    // 
    placeholder: {
      type: String,
      default: ''
    },
    // 
    clearable: {
      type: Boolean,
      default: true,
    },
    // 
    clearTrigger: {
      type: String,
      default: 'focus'
    },
    // iOS 
    autofocus: {
      type: Boolean,
      default: false,
    },
    // 
    showAction: {
      type: Boolean,
      default: false,
    },
    // 
    actionText: {
      type: [String, Boolean],
      default: ''
    },
        // 
    disabled: {
      type: Boolean,
      default: false,
    },
    // 
    readonly: {
      type: Boolean,
      default: false,
    },
    // 
    error: {
      type: Boolean,
      default: false,
    },
    // 
    inputAlign: {
      type: String,
      default: 'left'
    },
    // 
    leftIcon: {
      type: String,
      default: 'search'
    },
    // 
    rightIcon: {
      type: String,
      default: ''
    },
    //  
    interfaceUrl: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      // 
      isSearching: true,
      // 
      keyword: '',
      // 
      cascadeKeywords: [
        {
          html: '<span class="search-text"></span>'
        },
        {
          html: '<span class="search-text"></span>'
        },
        {
          html: '<span class="search-text"></span>'
        }
      ],
      // 
      hotKeywords: [
        {
          text: ''
        },
        {
          text: ''
        },
        {
          text: ''
        },
        {
          text: ''
        }
      ],
      // 
      historyKeywords: [
        {
          text: ''
        },
        {
          text: ''
        },
        {
          text: ''
        },
        {
          text: ''
        },
        {
          text: ''
        }
      ],
      // 
      searchResult: []
    }
  },
  computed:{
    background2() {
      if (this.background == 'theme' || this.background == '') {
        return this.$root.themesConfig[this.$root.setting.theme]['main'];
      }
      return this.background;
    }
  },
  created() {
    // 
    this.$onChange = _.throttle(this.onChange, 500, {
      leading: true,
      trailing: true
    });
  },
  methods: {
    // 
    // v-html
    changeColor(resultsList) {
      if (!this.keyword && !this.keyword.length) {
        this.cascadeKeywords = [];
        return;
      }
      this.cascadeKeywords = resultsList.map((item, index) => {
        item.html = item.name.replace(
          new RegExp(this.keyword, 'g'),
          `<span class="search-text">${this.keyword}</span>`
        );
        return item;
      })
    },
    onChange(value) {
      console.log(value)
    },
    onSearch() {
      this.isSearching = false;
      // this.$http.get(this.interfaceUrl, {
      //     searchValue: this.keyword
      // })
      //   .done(({data}) => {
      //     console.log('data', data);
      //   })
      //   .error(err => {
      //     dsf.layer.message("", false);
      //     this.finished = true;
      //   })
      //   .always(() => {

      //   })
    },
    onCancel() {
      if (this.isSearching && this.keyword != '') {
        this.isSearching = false;
        this.keyword = '';
      } else {
        this.$router.back();
      }
    },
    // 
    clearHistory() {

    },
    // 
    hotClick(item) {

    },
    // 
    historyClick(item) {

    }
  }
})
</script>