<template>
  <div class="ds-control mobile ds-pending-list" :class="{ 'ds-no-padding': !isDesign }">
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list v-model="loading" :finished="finished" @load="onLoad" :finished-text="list && list.length ? '' : ''">
        <div class="ds-pending-list-box">
          <template v-for="(item, index) in list">
            <slot v-bind="{ row: item, $index: index }">
              <div :key="index" class="ds-pending-list-row" :focus="item.focus == 1" :ontop="item.ontop == 1">
                <div class="ds-pending-list-row-box">
                  <div class="ds-pending-list-row-info" @click="onClick({ type: 'info', row: item, $index: index })">
                    <div class="ds-pending-list-row-title">
                      {{ item && item.title }}
                    </div>
                    <div class="ds-pending-list-label-box">
                      <div class="ds-pending-list-label">
                        <DsfIcon class="ds-pending-list-label-icon" name="wenjianjia" />
                        <div class="ds-pending-list-label-text">
                          {{ item && item.author }}
                        </div>
                      </div>
                      <div class="ds-pending-list-label">
                        <DsfIcon class="ds-pending-list-label-icon" name="calendar" />
                        <div class="ds-pending-list-label-text">
                          {{ item && item.createTime }}
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="ds-pending-list-row-bottom">
                    <div class="ds-pending-list-row-caozuo-left">
                      <div
                        class="ds-pending-list-row-caozuo-item"
                        @click.stop="
                          operatingClick({
                            type: 'gz',
                            row: item,
                            $index: index,
                          })
                        ">
                        <template v-if="item.focus == 0">
                          <DsfIcon class="ds-pending-list-row-caozuo-item-icon" name="xingzhuang-xingxing" />
                          <div class="ds-pending-list-row-caozuo-item-text">
                            
                          </div>
                        </template>
                        <template v-else>
                          <DsfIcon class="ds-pending-list-row-caozuo-item-icon" name="ditu-xing" />
                          <div class="ds-pending-list-row-caozuo-item-text">
                            
                          </div>
                        </template>
                      </div>
                      <div
                        class="ds-pending-list-row-caozuo-item"
                        @click.stop="
                          operatingClick({
                            type: 'zd',
                            row: item,
                            $index: index,
                          })
                        ">
                        <DsfIcon name="zhiding2" />
                        <div v-if="item.ontop == 0" class="ds-pending-list-row-caozuo-item-text">
                          
                        </div>
                        <div v-else class="ds-pending-list-row-caozuo-item-text">
                          
                        </div>
                      </div>
                    </div>
                    <div class="ds-pending-list-row-caozuo-right">
                      <van-button
                        class="ds-pending-list-row-caozuo-right-but"
                        type="info"
                        @click.stop="
                          operatingClick({
                            type: 'cz',
                            row: item,
                            $index: index,
                          })
                        ">
                        
                      </van-button>
                    </div>
                  </div>
                </div>
              </div>
            </slot>
          </template>
        </div>
        <template v-if="!list || !list.length">
          <van-empty slot:finished description="" />
        </template>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobilePendingList",
  ctrlCaption: "",
  mixins: [$mixins.control],
  props: {
    url: {
      type: String,
      default: ""
    },
    viewedUrl: {
      type: String,
      default: "/teas/message/viewed"
    },
    pageSize: {
      type: [Number, String],
      default: 10
    }
  },
  data() {
    return {
      refreshing: false,
      loading: false,
      finished: true,
      list: [],
      pageNum: 1
    };
  },
  created() {
    this.getList();
  },
  methods: {
    reloadData() {
      this.onRefresh();
    },
    onRefresh(noRefreshing) {
      this.pageNum = 1;
      this.finished = false;
      this.refreshing = !noRefreshing;
      this.getList(true);
    },
    onLoad() {
      this.pageNum += 1;
      this.loading = true;
      this.getList();
    },
    operatingClick(args) {
      if (this.$listeners["operatingClick"]) {
        this.$dispatch("operatingClick", args);
      } else {
        if (args.type == "zd") {
          this.ontop(args);
        } else if (args.type == "gz") {
          this.focus(args);
        } else if (args.type == "cz") {
          this.onClick({ args });
        }
      }
    },
    getList(isRefresh) {
      let { url, isDesign, pageSize, pageNum } = this;
      if (isDesign || !url) {
        this.finished = true;
        this.loading = false;
        this.refreshing = false;
        if (isDesign) {
          this.list = Array.from({ length: 10 }, (k, i) => ({
            author: "jinhy",
            content: "",
            createTime: "2021-02-24 13:51",
            linkid: "",
            mobileUrl: "",
            nodeid: null,
            objectid: "",
            pid: null,
            sign: [],
            state: "0",
            title: "[]",
            type: "1",
            url: "",
            wfid: "",
            _id: i + 1
          }));
        } else if (!url) {
          this.list = [];
        }
        return;
      }
      let list = [];
      let params = { pageSize, pageNum };
      this.$http
        .get(url, pageSize && params)
        .then(({ data }) => {
          this.refreshing = false;
          this.loading = false;
          if (data && data.success) {
            list = data.data || [];
            if (!list.length || list.length !== pageSize) {
              this.finished = true;
            } else {
              this.finished = false;
            }
          } else {
            this.finished = true;
            dsf.layer.message(data.message, false);
          }
          this.$set(this, "list", isRefresh ? list : this.list.concat(list));
        })
        .catch(() => {
          dsf.layer.message("", false);
          this.$set(this, "list", isRefresh ? list : this.list.concat(list));
        });
    },
    onClick(obj) {
      let { isDesign } = this;
      if (isDesign) return;
      let { type, row, $index } = obj;
      if (this.$listeners["itemClick"]) {
        return this.$dispatch("itemClick", obj);
      }
      if (type == "info") {
        this.toInfo(row, $index);
      }
    },
    redRow(id, type) {
      this.$http
        .get(this.viewedUrl, {
          id,
          type
        })
        .then((data) => {})
        .catch(() => {});
    },
    toInfo(info, $index) {
      let url = info.mobileUrl || "";
      const next = () => {
        if (!url && info.type == 1) {
          return dsf.layer.message("PC", false);
        }
        url && this.redRow(row._id, row.messageType || "");
        this.$open({ url });
      };
      if (info.type == 0) {
        this.$http
          .get("/wfp/getPageUrl", {
            isMobile: "1",
            sID: info.objectid,
            sPID: info.pid,
            sCurLinkID: info.linkid,
            sWFID: info.wfid,
            sNodeID: info.nodeid
          })
          .then(({ data }) => {
            if (data.success && data.data) {
              this.$open({ url: data.data });
            } else {
              dsf.layer.message(res.data.Message || "", false);
            }
          })
          .catch((res) => {
            dsf.layer.message("", false);
          });
      } else {
        next();
      }
    },
    ontop(args) {
      this.$http
        .post("/teas/message/ontop", {
          id: args.row._id,
          value: args.row.ontop == 1 ? 0 : 1
        })
        .then(({ data }) => {
          if (data && data.success) {
            dsf.layer.message("", true);
            this.reloadData();
          } else {
            dsf.layer.message(data.message, false);
          }
        })
        .catch(() => {
          dsf.layer.message("", false);
        });
    },
    focus(args) {
      this.$http
        .post("/teas/message/focus", {
          id: args.row._id,
          value: args.row.focus == 1 ? 0 : 1
        })
        .then(({ data }) => {
          if (data && data.success) {
            dsf.layer.message("", true);
            this.reloadData();
          } else {
            dsf.layer.message(data.message, false);
          }
        })
        .catch((err) => {
          dsf.layer.message("", false);
        });
    }
  }
});
</script>
